// living
const livingSwiper = new Swiper(".livingSwiper", {
    slidesPerView: 1.08,
    spaceBetween: 10,
    pagination: {
        el: ".living__swiper-pagination",
        clickable: true,
    },
    breakpoints: {
        1100: {
            slidesPerView: 4,
            spaceBetween: 10,
        },
        768: {
            slidesPerView: 3,
            spaceBetween: 10,
        },
        540: {
            slidesPerView: 2.08,
            spaceBetween: 10,
        },
    },
});

if (window.innerWidth < 768) {
    livingSwiper.enable()
} else {
    livingSwiper.disable()
}


// stages
const stagesSwiper = new Swiper(".stagesSwiper", {
    slidesPerView: 1.08,
    spaceBetween: 10,
    navigation: {
        nextEl: ".stages__swiper-button-next",
        prevEl: ".stages__swiper-button-prev",
    },
    pagination: {
        el: ".stages__swiper-pagination",
        clickable: true,
        renderBullet: function (index, className) {
            return '<span class="swiper-number ' + className + '">' + (index + 1) + "</span>";
          },
    },

    breakpoints: {
        768: {
            slidesPerView: 1,
            spaceBetween: 0,
        },
    },
});

